<template>
 <pulse-loader v-if="loading" style="margin-top: 60px;text-align: center;"></pulse-loader>
 <div v-else class="container-fluid">
     <div>
        <input class="form-control" type="text" v-model="search" placeholder="Search Labs.."/>
    </div>
    <div class="vitalsWrapper">
            <div class="card cardWrapper" v-bind:key="getRowKey(lab)" v-for="lab in filteredList">
            <div class="card-body">
                <a href="#"  data-toggle="modal" data-target='#exampleModel' @click="info(lab)">
                    <h5>{{lab}} <span class="badge badge-success badge-pill" style="float:right;">{{getCountofRecordsByLabCode(lab)}}</span></h5>
                </a>
            </div>
        </div>
    </div>
    <!-- Modal -->
    <div class="modal fade bd-example-modal-lg" id="exampleModel" tabindex="-1" role="dialog">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{infoModal.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="resetInfoModal()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <ObservationModal :infoModal="infoModal" :cchsCoreConfig="cchsCoreConfig"/>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="resetInfoModal()">Close</button>
                </div>
            </div>
        </div>
    </div>      
</div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
import ObservationModal from "../../components/ObservationModal";

export default {
    components:{
        PulseLoader,
        ObservationModal
    },
    props: ["smartClient","patient","cchsCoreConfig"],
    data(){
        return{
            search: null,
            labCodes: null,
            rowArray: [],
            laboratory: null,
            loading: true,
            infoModal: {
                title: '',
                content: '',
                isComponentTagPresent: false,
                getValuesByCodeText: []
            }
        }
    },
    computed:{
        
    },
    methods:{
        getRowKey(row){
            return row+Math.random();
        },
        groupByLabCodes(){
            this.labCodes = Array.from(new Set(this.laboratory.data.entry.map(a => a.resource.code.text)));
            /*let rowCount = 0;
            let count = 0;
            if(this.labCodes.length % 3 === 0){
                rowCount = this.labCodes.length/3;
                for (var i = 0; i < rowCount -1; i++) { 
                    this.rowArray.push(this.labCodes.slice(count, count + 3));
                    count+=3;
                }
            }else{
                rowCount = (this.labCodes.length/3) + 1;
                for (var i = 0; i < rowCount-1; i++) { 
                    if(count < this.labCodes.length)
                        this.rowArray.push(this.labCodes.slice(count, count + 3));
                    else
                        this.rowArray.push(this.labCodes.slice(count, this.labCodes.length));
                    count+=3;
                }
            }*/
        },
        getCountofRecordsByLabCode(lab){
            return this.laboratory.data.entry.filter(e => e.resource.code.text === lab).length;
        },
        resetInfoModal() {
            this.infoModal.title = ''
            this.infoModal.getValuesByCodeText = [];
            this.infoModal.isComponentTagPresent = false;
        },
        info(lab) {
            this.infoModal.title = lab;
            this.infoModal.getValuesByCodeText = this.laboratory.data.entry.filter(e => e.resource.code.text === lab)
        }
    },
    async beforeMount(){
        this.laboratory = await this.smartClient.patient.api.search({
            type:"Observation",
            query: {patient: this.patient.id,
                    category: "laboratory",
                    _count: 100
            }
          });
        this.groupByLabCodes();
        this.loading=false;
    },
    computed:{
        filteredList: function(){
            if(this.search){
                return this.labCodes.filter(e => {return e.toLowerCase().includes(this.search.toLowerCase())});
            }else{
                return this.labCodes;
            }
        }
    }
}
</script>

<style scoped>
.vitalsWrapper{
  -webkit-column-count: 3; /* Chrome, Safari, Opera */
  -moz-column-count: 3; /* Firefox */
  column-count: 3;
  -webkit-column-gap: 40px; /* Chrome, Safari, Opera */
  -moz-column-gap: 40px; /* Firefox */
  column-gap: 40px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 12px;
}
.cardWrapper{
    width: 350px;
    height: 100px;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
}
</style>
