<template>
    <div v-if="this.viewType==='CardView'" class="container-fluid">
        <div class="row pl-0 h-150px"  v-bind:key="getRowKey(row)" v-for="row in rowArray">
            <div class="col-sm-4 pl-0" v-bind:key="getRowKey(col)" v-for="col in row">
                <div class="card">
                    <div class="card-body">
                        <h5 class="card-title">{{col.resource.code.text}}</h5>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="container-fluid" v-else-if="this.viewType==='TableView'">
        <div class="row mb-2">
            <div class="col pl-0">
                <input class="form-control" type="text" v-model="search" placeholder="Search Condition.."/>
            </div>
        </div> 
        <div class="row">
            <div class="col-sm-12">
                <table class="table table-striped">
                    <thead >
                        <tr>
                            <th scope="col">Condition</th>
                            <th scope="col">Verification</th>
                            <th scope="col">Asserter</th>
                            <th scope="col">Onset Time</th>
                        </tr>
                    </thead>
                    <tbody v-bind:key="cchsCoreConfig.getRowKey(row.resource.id)" v-for="row in filteredList">
                        <td>{{row.resource.code.text}}</td>
                        <td>{{row.resource.verificationStatus}}</td>
                        <td>{{row.resource.asserter.display}}</td>
                        <td>{{cchsCoreConfig.getDateInReadableFormat(row.resource.onsetDateTime)}}</td>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:["summaryModel", "viewType","cchsCoreConfig"],
    data(){
        return{
            rowArray: [],
            search: null
        }
    },
    methods:{
        dataInit(){
            let rowCount = 0;
            let count = 0;

            if(this.summaryModel.array.length % 3 == 0){
                rowCount =  this.summaryModel.array.length / 3;
                for (var i = 0; i < rowCount-1; i++) {
                    this.rowArray.push(this.summaryModel.array.slice(count, count + 3));
                    count+=3;
                }
            }else{
                rowCount =  this.summaryModel.array.length / 3 + 1;
                for (var i = 0; i < rowCount-1; i++) {
                    if(count < this.summaryModel.array.length)
                        this.rowArray.push(this.summaryModel.array.slice(count, count + 3));
                    else
                        this.rowArray.push(this.summaryModel.array.slice(count, this.summaryModel.array.length));
                    count+=3;
                }
            }
        }
    },
    beforeMount(){
        if(this.viewType==='CardView')
            this.dataInit();
        //v-bind:key="diag.resource.id" v-for="diag in summaryModel"
    },
    computed:{
        filteredList: function(){ 
            if(this.search){
                return this.summaryModel.array.filter(e => {return e.resource.code.text.toLowerCase().includes(this.search.toLowerCase())});
            }else{
                return this.summaryModel.array;
            }
        }
    }
}
</script>

<style>
.thead-darkgreen{
    background: darkgreen;
    color: whitesmoke;
}
.thead-darkgreen th{
    font-weight: normal;
}
</style>
