<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-sm-12">
                <table class="table table-striped">
                    <thead class="thead-darkgreen">
                        <tr>
                            <th scope="col">Id#</th>
                            <th scope="col">Encounter#</th>
                            <th scope="col">Value</th>
                            <th scope="col">Issued Time</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-bind:key="v.resource.id" v-for="v in infoModal.getValuesByCodeText">
                            <td>{{v.resource.id}}</td>
                            <td>{{v.resource.encounter.reference.replace("Encounter/","")}}</td>
                            <td v-if="v.resource.component!==undefined">
                                <ul>
                                    <li  v-bind:key="com.code.text" v-for="com in v.resource.component">
                                        {{com.code.text.replace("Blood Pressure","")}}
                                        {{com.valueQuantity !== undefined ? 
                                            com.valueQuantity.unit !== undefined ? 
                                            com.valueQuantity.value +' '+ com.valueQuantity.unit : 
                                            com.valueQuantity.value : com.valueString!==undefined ? 
                                            com.valueString : ''}}
                                    </li>
                                </ul>
                            </td>
                            <td v-else-if="v.resource.valueQuantity!==undefined">
                                {{v.resource.valueQuantity.value}}
                                {{v.resource.valueQuantity.unit}}
                            </td>
                            <td v-else-if="v.resource.valueCodeableConcept!==undefined">
                                {{v.resource.valueCodeableConcept.text}}
                            </td>
                            <td v-else>
                                {{v.resource.valueString!==undefined ? v.resource.valueString : 'NA'}}
                            </td>
                            <td>{{cchsCoreConfig.getDateInReadableFormat(v.resource.issued)}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ["infoModal","cchsCoreConfig"]
}
</script>

<style scoped>
.thead-darkgreen{
    background: darkgreen;
    color: whitesmoke;
}
.thead-darkgreen th{
    font-weight: normal;
}
</style>
