import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from './views/home/Dashboard.vue'
import VitalSigns from './views/observation/VitalSigns.vue'
import Laboratory from './views/observation/Laboratory.vue'
import ViewConditions from './views/condition/ViewConditions.vue'
import DocumentReference from './views/document/DocumentReference.vue'
import MedicalStatements from './views/MedicalStatements.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/laboratory',
      name: 'laboratory',
      component: Laboratory
    },
    {
      path: '/documentReference',
      name: 'documentReference',
      component: DocumentReference
    },
    {
      path: '/medicalStatements',
      name: 'medicalStatements',
      component: MedicalStatements
    },
    {
      path: '/vitalSigns',
      name: 'vitalSigns',
      component: VitalSigns
    },
    {
      path: '/viewConditions',
      name: 'viewConditions',
      component: ViewConditions
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})