<template>
    <div class="dateRangeWrapper">
        <div class="card" style="margin-bottom:10px;">
            <h5 class="card-header">Date Range</h5>
            <div class="card-body">
                <ul class="nav nav-pills justify-content-end">
                    <li class="nav-item">
                        <a id="sixMonthsId" :class="active_key == 'sixMonthsId' ? 'nav-link nav-link-active': 'nav-link'" href="#" @click="toggleClass('sixMonthsId', .5)">Six months</a>
                    </li>
                    <li class="nav-item">
                        <a id="oneYearId" :class="active_key == 'oneYearId' ? 'nav-link nav-link-active': 'nav-link'" href="#" @click="toggleClass('oneYearId', 1)">1 Year</a>
                    </li>
                    <li class="nav-item">
                        <a id="twoYearId" :class="active_key == 'twoYearId' ? 'nav-link nav-link-active': 'nav-link'" href="#" @click="toggleClass('twoYearId', 2)">2 Year</a>
                    </li>
                    <li class="nav-item">
                        <a id="allYearsId" :class="active_key == 'allYearsId' ? 'nav-link nav-link-active': 'nav-link'" href="#" @click="toggleClass('allYearsId', 10)">All</a>
                    </li>
                </ul>
            </div>
            <div class="card-body pr-5 pl-5">
                <vue-slider v-model="value" :data="data" tooltip="always" :lazy=true :tooltip-placement="['top', 'bottom']" :dragging="dateRangeChange(value)">
                </vue-slider>
                <div class="form-row" style="padding-top:40px;">
                    <div class="form-group col-md-4">
                        <label style="padding-right:15px;" for="inputEmail4">From</label>
                        <date-pick v-model="pickerStartDate" :displayFormat="'MM/DD/YYYY'"></date-pick>
                    </div>
                    <div class="form-group col-md-4">
                        <label style="padding-right:15px;">To</label>
                        <date-pick v-model="pickerEndDate" :displayFormat="'MM/DD/YYYY'"></date-pick>
                    </div>
                    <div class="form-group col-md-4">
                        <button type="button" class="btn btn-info" @click="searchFromDatePicker()">Search</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import moment from 'moment'
import Utils from '../cchs-utils'
import DatePick from 'vue-date-pick';
import 'vue-date-pick/dist/vueDatePick.css';

export default {
    components:{DatePick},
    data(){
        return {
            startDate: undefined,
            endDate: undefined,
            value: undefined,
            data: undefined,
            active_key: '',
            pickerEndDate:"",
            pickerStartDate:""
        }
    },
    methods:{
        getDates(start, end) {
            var dateArray = [];
            var startDate = moment(start);
            var stopDate = moment(end);
            while (startDate.diff(stopDate) >= -1) {
                //console.log("FIRST: "+startDate.diff(stopDate))                
                dateArray.push( moment(startDate).format('YYYY-MM-DD') )
                startDate = moment(startDate).subtract(24, 'hours');
                //console.log("SECOND: "+startDate.diff(stopDate))
            }
            return dateArray;
        },
        toggleClass(key, offset){
            this.active_key = key;
            this.startDate = new moment();
            this.endDate = moment().subtract(offset, 'years');
            this.value = [Utils.getDateInReadableFormat(this.startDate, moment.HTML5_FMT.DATE), Utils.getDateInReadableFormat(this.endDate, moment.HTML5_FMT.DATE)];
            this.data = this.getDates(this.startDate, this.endDate);
        },
        dateRangeChange(range){
            //console.log('THIS IS CALLED IN CHILD '+range)
            this.$emit('date-range-change', range);
        },
        searchFromDatePicker(){
            if(this.pickerStartDate !== "" && this.pickerEndDate !==""){
                let arr = [Utils.getDateInReadableMMDDYYYYFormat(this.pickerStartDate),Utils.getDateInReadableMMDDYYYYFormat(this.pickerEndDate)];
                this.dateRangeChange(arr);
            }
        }
    },
    computed:{
    },
    beforeMount(){
        this.startDate = new moment();
        this.endDate = moment().subtract(.5, 'years');
        this.value = [Utils.getDateInReadableFormat(this.startDate, moment.HTML5_FMT.DATE), Utils.getDateInReadableFormat(this.endDate, moment.HTML5_FMT.DATE)];
        this.data = this.getDates(this.startDate, this.endDate);
        this.active_key = 'sixMonthsId';
    }
}
</script>

<style>
.merge-tooltip {
    position: absolute;
    left: 50%;
    bottom: 100%;
    transform: translate(-50%, -15px);
    white-space: nowrap;
    color: #fff;
    background-color: inherit;
    padding: 3px 10px;
    border-radius: 15px;
}
.nav-link-active{
    color: #fff !important;
    background-color: #3498db;
}
.dateRangeWrapper{
  padding-left: 15px;
  padding-right: 15px;
}
.vdpComponent{
    font-size: medium;    
}
.vdpInnerWrap{
    font-size: 10px !important;
}
.form-group .vdpClearInput{
    width: 4em;
}
.vdpTable{
    width: inherit;
}
</style>