<template>
    <div id="patient">
      <div class="p-2" style="text-align:center;">
        <img v-if="this.patient.gender === 'male'" class="rounded-circle" width="150px" height="150px" src="../../public/assets/male_avatar.png"/>
        <img v-if="this.patient.gender === 'female'" class="rounded-circle" width="150px" height="150px" src="../../public/assets/female_avatar.png"/>
      </div>
      <div style="text-align:center;">
        <h5 >
          {{patient.name[0].given.join(' ')}} {{patient.name[0].family.join(' ')}} 
        </h5>
        <h6>{{this.getAge}} years {{patient.gender}}</h6>
        <span>
          ID:- {{this.patient.id}} 
        </span>
        <br/>
        <span>
          MRN:- {{this.findMRN}}
        </span>
        <br/>
        <span>Born:- {{this.patient.birthDate}}</span>
      </div>
    </div>
</template>

<script>
	export default {
			name: "Patient",
      props: ["patient"],
      computed:{
        findMRN: function(){
          return this.patient.identifier.find(e => e.type.text == 'MRN').value
        },
        getAge: function(){
            var today = new Date();
            var birthDate = new Date(this.patient.birthDate);
            var age = today.getFullYear() - birthDate.getFullYear();
            var m = today.getMonth() - birthDate.getMonth();
            if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
                age = age - 1;
            }

            return age;
        }
      }
	}
</script>

<style scoped>
#patient p{
  color: black;
}
</style>