<template>
    <pulse-loader v-if="loading" style="margin-top: 60px;text-align: center;"></pulse-loader>
    <div v-else id="parentAccordion" role="tablist" aria-multiselectable="true">
        <div class="card">
            <h5 class="card-header mb-0" role="tab" id="headingOne">
                <a class="d-block" data-toggle="collapse" href="#collapseOne" data-parent="#parentAccordion" aria-expanded="true" aria-controls="collapseOne">
                    <i class="fa fa-chevron-down"></i>
                    &nbsp; Diagnosis
                    <span class="badge badge-light badge-pill float-right">{{diagnosisTotal}}</span> 
                </a>
            </h5>
            <div id="collapseOne" class="collapse show" role="tabpanel"  aria-labelledby="headingOne">
                <div class="card-body">
                    <SummaryView :summaryModel="summaryModel" :viewType="'TableView'" :cchsCoreConfig="cchsCoreConfig"/>
                </div>
            </div>
        </div>
        <div class="card">
            <h5 class="card-header mb-0" role="tab" id="headingTwo">
                <a class="collapsed d-block" data-toggle="collapse" href="#collapseTwo" data-parent="#parentAccordion" aria-expanded="false" aria-controls="collapseTwo">
                    <i class="fa fa-chevron-down"></i>
                    &nbsp; Problems
                    <span class="badge badge-light badge-pill float-right">{{problemsTotal}}</span>
                </a>
            </h5>
            <div id="collapseTwo" class="collapse" role="tabpanel"  aria-labelledby="headingTwo">
                <div class="card-body">
                    <SummaryView :summaryModel="summaryModel1" :viewType="'TableView'" :cchsCoreConfig="cchsCoreConfig"/>
                </div>
            </div>
        </div>
        <div class="card">
            <h5 class="card-header mb-0" role="tab" id="headingThree">
                <a class="collapsed d-block" data-toggle="collapse" href="#collapseThree" data-parent="#parentAccordion" aria-expanded="false" aria-controls="collapseThree">
                    <i class="fa fa-chevron-down"></i>
                    &nbsp; Health Concern
                    <span class="badge badge-light badge-pill float-right">{{healthConcernTotal}}</span>
                </a>
            </h5>
            <div id="collapseThree" class="collapse" role="tabpanel"  aria-labelledby="headingThree">
                <div class="card-body">
                    <SummaryView :summaryModel="summaryModel2" :viewType="'TableView'" :cchsCoreConfig="cchsCoreConfig"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
import SummaryView from '../../components/Summary.vue';

export default {
  components:{
        PulseLoader,
        SummaryView
    },
    props: ["cchsCoreConfig","patient","cchsCoreConfig"],
    data(){
        return{
            summaryModel:{
                array: null,
                link: []
            },
             summaryModel1:{
                array: null,
                link: []
            },
             summaryModel2:{
                array: null,
                link: []
            },
            loading: true,
            diagnosisTotal: 0,
            problemsTotal: 0,
            healthConcernTotal: 0
        }
    },
    methods:{
        fetchInitData(categoryValue){
            if(this.summaryModel.array === null || this.summaryModel1.array === null || this.summaryModel2.array === null){
                this.cchsCoreConfig.instance.get("Condition",{params:{patient: this.patient.id, category:categoryValue}})
                .then(response => {
                    if(response.status === 200){
                        switch(categoryValue){
                            case 'diagnosis':
                                this.summaryModel.array = response.data.entry.filter(e=> e.resource.verificationStatus !== 'entered-in-error');
                                this.summaryModel.link = response.data.link;
                                this.diagnosisTotal = this.summaryModel.array.length;
                                break;
                            case 'problem':
                                this.summaryModel1.array = response.data.entry.filter(e=> e.resource.verificationStatus !== 'entered-in-error');
                                this.summaryModel1.link = response.data.link;
                                this.problemsTotal = this.summaryModel1.array.length;
                                break;
                            case 'health-concern':
                                this.summaryModel2.array = response.data.entry.filter(e=> e.resource.verificationStatus !== 'entered-in-error');
                                this.summaryModel2.link = response.data.link;
                                this.healthConcernTotal = this.summaryModel2.array.length;
                                break;
                            
                        }
                        this.loading = false;
                    }
                },
                error_response => {
                    console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
                });
            }
        }
    },
    beforeMount(){
      this.fetchInitData('diagnosis');
      this.fetchInitData('problem');
      this.fetchInitData('health-concern');
    }
}
</script>

<style>
#parentAccordion .card-header .fa {
  transition: .3s transform ease-in-out;
}
#parentAccordion .card-header .collapsed .fa {
  transform: rotate(-90deg);
}
#parentAccordion .card-header{
  color: whitesmoke;
  background: darkslateblue;
}
#parentAccordion .card-header:hover{
  color: whitesmoke;
  background: slateblue;
}
</style>
