<template>
    <div class="d-flex flex-row medicationWrapper" ref="medication">
        <div class="medicationLine" :style="'width:'+medCapsuleWidth+'%; background-color:'+getMedCapsuleColor">

        </div>
        <!-- <div class="p-2 medDetailsCapsule" type="button"
             v-for="med in medicationMapArr" 
             v-bind:key="med.id">
            <h6 @click="toggleMedDetails(med)">{{med.dosage[0].text}}</h6>
            <div :id="med.id" v-if="med.id === showHideMedDetails"  class="medDetailsWrapper">
                <h6>Medication Details</h6>
                <p><b>Taken: </b>{{!med.wasNotTaken ? 'Yes': 'No'}}</p>
                <p><b>Status: </b>{{getCapitalizeValue(med.status)}}</p>
                <p><b>Source: </b>{{getCapitalizeValue(med.informationSource !=undefined ? med.informationSource.display : 'NA')}}</p>
                <p><b>Asserted On: </b>{{getDate(med.dateAsserted)}}</p>
                <p><b>Effective From: </b>{{getDate(med.effectivePeriod.Start)}}</p>
                <p><b>Effective To: </b>{{getDate(med.effectivePeriod.end)}}</p>
            </div>
        </div> -->
    </div>
</template>

<script>
import Utils from "../../cchs-utils";
import moment from'moment';
export default {
    name:'MedicationTimeLine',
    data(){
        return {
            showHideMedDetails: undefined,
            medDetailsHTML: '',
            width: undefined
        }
    },
    props:['medicationMapArr', 'patient', 'cchsCoreConfig'],
    methods:{
        calculateWidth() {
            this.width = this.$refs.medication.clientWidth - 300;
        },
        toggleMedDetails(med){
            if(this.showHideMedDetails !== undefined){
                this.showHideMedDetails = undefined;
                this.medDetailsHTML = '';
            }
            else{
                this.showHideMedDetails = med.id;
                this.medDetailsHTML = med.text.div;
                console.log('med.id:: '+med.id)
            }
        },
        makeMedDetailsDiv(med){
            return "<div>"+  +"</div>"
        },
        getCapitalizeValue(input){
            return Utils.capitalize(input)
        },
        getDate(input){
            return Utils.getDateInReadableFormat(input,"MMMM Do YYYY, h:mm:ss a")
        }
    },
    computed:{
        getMedCapsuleColor(){
            return 'rgb(' + (Math.floor(Math.random() * 150)) + ',' + (Math.floor(Math.random() * 150)) + ',' + (Math.floor(Math.random() * 150)) + ')';
        },
        medicationTotalDuration(){
            let currentDate = moment();
            let firstDate = moment(this.medicationMapArr[0].dateAsserted);
            return Math.round(moment.duration(currentDate.diff(firstDate)).as('hours')/24);
        },
        medCapsuleWidth(){
            let value = Utils.normalize(this.medicationTotalDuration, this.width, 0);
            return value * 100 > 100 ? 100 : value*100;
        }
    },
    mounted(){
        this.calculateWidth();
    }
}
</script>

<style>
.medDetailsWrapper{
    position: absolute;
    background: khaki;
    width: 300px;
    border: 1px solid khaki;
    border-radius: 4px;
    padding: 10px 20px;
    box-shadow: 0 6px 6px rgba(16, 16, 16, 0.04), 0 6px 6px rgba(0, 0, 0, 0.05);
    z-index:999;
    top: 0px;
    left: 100%;
    margin-left: 10px;
}
.medDetailsWrapper::before {
    content: "";
    position: absolute;
    left: -20px;
    top: 10px;
    border-width: 10px;
    border-style: solid;
    border-color: khaki transparent transparent transparent;
    transform: rotate(90deg);
}
.medDetailsWrapper p{
    color: black !important;
}
.medDetailsWrapper p b{
    color: black !important;
}
.medDetailsCapsule{
    display: inline-block;
    position: relative;
}
.medicationWrapper{
    background-color: rgba(205, 230, 255, 0.2);
    height: 50px;;
}
.medicationLine{
    height: 50% !important;
    margin-top:12px;
    border-style: inset;
    border-width: 1px;
    margin-left: 10px;
}
</style>