<template>
    <div class="form-group">
        <div class="form-group row">
            <div class="col-5">
                <label><small>Location:</small> &nbsp;</label>
                <label>{{location}}</label>
            </div>
            <div class="col-4">
                <label><small>Start Date:</small> &nbsp;</label>
                <label>{{start_date}}</label>
            </div>
             <div class="col-3">
                <label><small>Visit / Status:</small> &nbsp;</label>
                <label>{{selectedEncounter.class.charAt(0).toUpperCase() + selectedEncounter.class.slice(1)+' / '+
                    selectedEncounter.status.charAt(0).toUpperCase() + selectedEncounter.status.slice(1)}}</label>
            </div>
        </div>
        <div class="row">
            <div class="col-5">
                <label><small>Reason For Visit:</small> &nbsp;</label>
                <label>{{reason_for_visit}}</label>
            </div>
            <div class="col-4">
                <label><small>End Date:</small> &nbsp;</label>
                <label>{{end_date}}</label>
            </div>
            <div class="col-3">
                <label><small>Attending Physician:</small> &nbsp;</label>
                <label>{{attending_physician}}</label>
            </div>
        </div>
    </div>
  <!-- <div v-html="selectedEncounter.text.div"/> -->
</template>

<script>
import Utils from '../../cchs-utils'
export default {
    name: 'EncounterDetails',
    props:['selectedEncounter','cchsCoreConfig'],
    computed: {
        start_date: function(){
            return Utils.getDateInReadableFormat(this.selectedEncounter.period.start,"MMMM Do YYYY, h:mm:ss a");
        },
        end_date: function(){
            return Utils.getDateInReadableFormat(this.selectedEncounter.period.end, "MMMM Do YYYY, h:mm:ss a");
        },
        location: function(){
            if(this.selectedEncounter.location.length !==0)
                return this.selectedEncounter.location.pop().location.display;
            else
                return 'NA'
        },
        attending_physician: function(){
            if(this.selectedEncounter.participant !== undefined)
                return this.selectedEncounter.participant.find(e => {
                return e.type.find(c => { return c.text === 'Attending Physician'})
                }).individual.display
            else
                return ''
        },
        reason_for_visit: function(){
            let reason = '';
            if(this.selectedEncounter.reason !== undefined)
                this.selectedEncounter.reason.forEach(e => {reason+=e.text})
            return reason;
        }
    }
}
</script>

