<template>
<div id="dashboard" class="container-fluid">
    <div class="dashboardWrapper" >
        <div class="sampleContainer">
            <EncounterPanel style="margin-bottom: 10px;" :patient="patient" :cchsCoreConfig="cchsCoreConfig"/>
            <MedicationPanel style="margin-bottom: 10px;" :patient="patient" :cchsCoreConfig="cchsCoreConfig"/>
        </div>
    </div>
    <!-- <div class="form-group">
        <label for="exampleFormControlSelect1">Encounters</label>
        <select class="form-control" style="text-transform: capitalize;" v-model="encounter">
            <option disabled value="">Please select one</option>
            <option v-bind:key="enctr.id" 
                    v-for="enctr in encountersList" 
                    :value="enctr.id">
                    {{"FIN-> "+getFin_NbrNumber(enctr.identifier)+' , Type-> '+enctr.class +' , Status-> '+enctr.status + ' , Count-> '+showCount("Encounter/"+enctr.id)}}
            </option>
        </select>
    </div>

    <div class="dashboardWrapper" v-if="showDashboard">
        <div class="sampleContainer">
            <EncounterPanel style="margin-bottom: 10px;" :filteredList="filteredVitalsList" :cchsCoreConfig="cchsCoreConfig"/>
            <VitalsHistory style="margin-bottom: 10px;" :filteredList="filteredVitalsList" :cchsCoreConfig="cchsCoreConfig"/>
            <LaboratoryHistory style="margin-bottom: 10px;" :filteredLabList="filteredLabList" :cchsCoreConfig="cchsCoreConfig"/>
        </div>
    </div>

    <div  v-else-if="error_response === null && !this.showDashboard" class="alert alert-warning alert-dismissible fade show" role="alert">
        No Data present for this encounter
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
    </div> -->

    <div  v-if="error_response !== null" class="alert alert-danger alert-dismissible fade show" role="alert">
        <strong>{{error_response}}</strong>
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
    </div>

</div>
</template>

<script>
import EncounterPanel from "../../components/encounter/EncounterPanel";
import MedicationPanel from "../../components/medications/MedicationPanel";
import VitalsHistory from "../../components/VitalsHistory";
import LaboratoryHistory from "../../components/LaboratoryHistory";

import Utils from "../../cchs-utils";

export default {
    name: 'dashboard',
    components:{LaboratoryHistory, VitalsHistory, EncounterPanel, MedicationPanel},
    data(){
        return{
            search: null,
            encounter: '',
            labsPanelList: [],
            vitalsPanelList: [],
            vitalsURIList: [],
            error_response: null
        }
    },
    props: ["encountersList", "patient", "cchsCoreConfig"],
    methods:{
        showCount(reference){
            return this.labsPanelList.filter(e => e.encounter.reference === reference).length + this.vitalsPanelList.filter(e => e.encounter.reference === reference).length
        },
        getFin_NbrNumber(identifier){
            if(identifier !== undefined)
                return identifier.filter(e => e.type.text === 'FIN NBR').pop().value;
            else
                return 'n/a'
        },
        fetchAllLabPanels(){
            let params = {patient: this.patient.id,  category:'laboratory',code: this.cchsCoreConfig.APP_CONFIG.CBC_PANEL_CODE+','+this.cchsCoreConfig.APP_CONFIG.CHEM7_PANEL_CODE}
            const fetchAllLabs = new Promise((resolve, reject) =>{
                Utils.fetchAllRecords(this.cchsCoreConfig.instance,'Observation', params, [], resolve, reject);
            }).then(response=> this.labsPanelList = response);
        },
        fetchAllVitalSigns(){
            let params = {patient: this.patient.id,  category:'vital-signs', code: this.cchsCoreConfig.APP_CONFIG.VITALS_PANEL_CODE}
            const fetchAllVitals = new Promise((resolve, reject) =>{
                Utils.fetchAllRecords(this.cchsCoreConfig.instance,'Observation', params, [], resolve, reject);
            }).then(response=> this.vitalsPanelList = response);
        }
    },
    beforeMount(){
        //this.fetchAllLabPanels();
        //this.fetchAllVitalSigns();
    },
    computed:{
        encounterResource: function(){
            return "Encounter/"+this.encounter;
        },
        filteredLabList: function(){
            if(this.encounter)
                return this.labsPanelList.filter(e => e.encounter.reference === this.encounterResource)
        },
        filteredVitalsList: function(){
            if(this.encounter)
                return this.vitalsPanelList.filter(e => e.encounter.reference === this.encounterResource)
        },
        showDashboard: function(){
            let retValue = false;
                if((this.filteredLabList !== undefined && 
                    this.filteredLabList.length !==0) ||
                   (this.filteredVitalsList !== undefined && 
                    this.filteredVitalsList.length !==0)){
                        retValue = true;
                    }
            return retValue;
        }
    }
}
</script>

<style>
.dashboardWrapper{
    display: flex;
    flex-wrap: wrap;
}
.sampleContainer{
    width: 100%;
    height: 100%;   
}
</style>
