<template>
    <div> 
        <h6>{{heading}}</h6>
            <svg width="100%" height="100">
                <rect x="0" y="5" width="100%" height="55" style="fill:rgba(205,230,255,0.8)" />
                <svg class="encounterRect" :class="{ fill_icon : active_key == enctr.id }" v-bind:key="enctr.id" v-for="enctr in filteredEncountersList" @click="showEncounterDetails(enctr, enctr.id)" >
                    <icon-base :x="enctr.x" y="10" width="25" height="25" icon-name="Hospital" :iconColor="active_key == enctr.id ? 'maroon':'midnightblue'">
                        <icon-write :dValue="'M2,22V7A1,1 0 0,1 3,6H7V2H17V6H21A1,1 0 0,1 22,7V22H14V17H10V22H2M9,4V10H11V8H13V10H15V4H13V6H11V4H9M4,20H8V17H4V20M4,15H8V12H4V15M16,20H20V17H16V20M16,15H20V12H16V15M10,15H14V12H10V15Z'"/>
                    </icon-base>
                    <text :x="enctr.x" y="50">{{enctr.start}}</text>
                </svg>
            </svg>
        <div class="alert alert-info alert-dismissible" v-if="isEncounterDetailsPresent">
            <EncounterDetails :selectedEncounter="selectedEncounter" :cchsCoreConfig="cchsCoreConfig"/>
            <EncounterNotesCarousel  v-if="document.documentReferenceArray.length !==0" 
                                    :selectedEncounter="selectedEncounter" 
                                    :cchsCoreConfig="cchsCoreConfig" 
                                    :document="document"/>
            <button type="button" class="close" @click="hideEncounterDetails()" aria-label="Close">
                <span aria-hidden="true">&times;</span>
            </button>
        </div>
        
    </div>
</template>

<script>
import EncounterDetails from './EncounterDetails'
import EncounterNotesCarousel from './EncounterNotesCarousel'
import Utils from '../../cchs-utils'
import IconBase from '../icons/IconBase'
import IconWrite from '../icons/IconWrite'
export default {
    name: 'EncounterTimeLine',
    props: ['heading', 'filteredEncountersList', 'cchsCoreConfig'],
    components:{EncounterDetails, EncounterNotesCarousel, IconWrite, IconBase},
    data(){
        return {
            isEncounterDetailsPresent: false,
            selectedEncounter: undefined,
            active_key: 0,
            document:{
                documentReferenceArray: [],
                link: []
            }
        }
    },
    methods:{
        showEncounterDetails(e,key){
            this.toggleEncounterDetails(e, key);// show hide
        },
        fetchDocumentsReferences(e){
            this.cchsCoreConfig.instance.get("DocumentReference",{params:{patient: this.cchsCoreConfig.patient.id, encounter: e.id, _count:3}})
            .then(response=>{
                if(response.status==200){
                    this.document.documentReferenceArray = Utils.processResponseData(response.data.entry);
                    this.document.link = response.data.link;
                    this.isEncounterDetailsPresent = true;
                    this.selectedEncounter = e;
                }
            }, error_response=>{
                console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
            });
        },
        toggleEncounterDetails(e, key){
            if(this.isEncounterDetailsPresent){
                this.isEncounterDetailsPresent =false;
                this.selectedEncounter = undefined;
                this.document.documentReferenceArray = [];
                this.document.link = [];
                this.active_key = 0;
            }else{
                this.fetchDocumentsReferences(e);
                this.active_key = key;
            }
        },
        hideEncounterDetails(){
            this.isEncounterDetailsPresent = false;
            this.selectedEncounter = undefined;
            this.document.documentReferenceArray = [];
            this.document.link = [];
            this.active_key = 0;
        }
    }
}
</script>

<style>
.alert:hover{
   /* background-color: black !important;
   color: whitesmoke !important; */
}
.encounterRect{
    cursor: pointer;
    font-size: .75vw;
}
.fill_icon{
  fill: maroon;
}
</style>