<template>
  <div id="app">    
    <pulse-loader v-if="loading" style="margin-top: 60px;text-align: center;"></pulse-loader>
    <error v-else-if="error" :error="error" style="margin-top: 60px;text-align: center;"></error>

    <div v-else class="wrapper">
      <!-- <div id="sidebar-wrapper" v-bind:class="{ active: showSideBar}">
        <Sidebar :patient="patient"/>
      </div> -->
      <div id="content" class="container-fluid" v-bind:class="{active: !showSideBar}">
          <!-- <div class="row">
            <nav class="col-md-12 navbar navbar-light bg-light">
              <button type="button"  class="btn btn-success" v-on:click="showSideBar = !showSideBar">
                <i class="fas fa-bars"></i>
                  {{showSideBar ? 'Show Menu' : 'Hide Menu'}}
              </button>
            </nav>
          </div> -->
          <div class="row dateRangeRow">
            <div class="col-md-12">
              <DateRangeSlider v-on:date-range-change="dateRangeChange"/>
            </div>
          </div>
          <div class="row pageContentRow">
            <div id="page-content-wrapper" class="col-md-12">
                <router-view
                  :patient="patient" 
                  :medicationCodes="medicationCodes"
                  :medications="medications"
                  :cchsCoreConfig="cchsCoreConfig"
                  :smartClient="smartClient"/>
            </div>
          </div>
      </div>
    </div> 
  </div>
</template>

<script>
import axios from "axios";
import Error from './components/Error';
import Sidebar from './layout/Sidebar';
import DateRangeSlider from './layout/DateRangeSlider';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';

import cchsSmartClient from './cchs-smart-client';
import Core from './cchs_smartonfhir_core';

export default {
  name: 'app',
  components: {
    Error,
    DateRangeSlider,
    PulseLoader
  },
  data(){
  	return {
      medicationCodes: [],
      patient: null,
      encountersList: null,
      medications: null,
  		loading: true,
      error: null,
      tokenResponse: null,
      showSideBar: false,
      smartClient: null,
      cchsCoreConfig: null
  	}
  },
  async mounted(){
  		try{
          const smart = await cchsSmartClient();
          this.patient = await smart.patient.read();
          this.tokenResponse =  smart.tokenResponse;
          this.smartClient =  await smart;
          setInterval(this.refreshAccessToken, smart.tokenResponse.expires_in*1000);
          
          Core.APP_CONFIG.BASE_FHIR_API_URL = smart.state.provider.url;
          Core.APP_CONFIG.TOKEN_URI = smart.state.provider.oauth2.token_uri;
          Core.APP_CONFIG.AUTHORIZE_URI = smart.state.provider.oauth2.authorize_uri;
          Core.APP_CONFIG.ACCESS_TOKEN = smart.tokenResponse.access_token;

          Core.APP_CONFIG.CODE = smart.tokenResponse.code;
          Core.APP_CONFIG.STATE = smart.tokenResponse.state;

          Core.instance = axios.create({
            baseURL: Core.APP_CONFIG.BASE_FHIR_API_URL,
            headers: {'Accept': Core.APP_CONFIG.ACCEPT_APP_JSONFHIR, 
                      'Content-Type': Core.APP_CONFIG.CONTENT_TYPE_URLENCODED, 
                      'Authorization': Core.APP_CONFIG.TOKEN_TYPE +' '+ Core.APP_CONFIG.ACCESS_TOKEN}
          });
          Core.patient = this.patient;

          if(sessionStorage.getItem(Core.APP_CONFIG.GRANT_TYPE) == null)
            sessionStorage.setItem(Core.APP_CONFIG.GRANT_TYPE, smart.tokenResponse.refresh_token);

          console.log("PATIENT ID FROM EHR:- "+ this.patient.id);
          
          
          /*this.encountersList = await smart.patient.api.fetchAll({
              type:"Encounter",
              query: {patient: this.patient.id
            }
          });*/
          this.loading = false;
          this.cchsCoreConfig = Core;
  		} catch(resp){
        console.log("resp" + resp.error)
  				this.loading = false;
      		this.error = resp.error || resp;
      }
  },
  methods:{
    refreshAccessToken(){
     console.log("Send a Request for New Access Token.");
     var querystring = require('querystring');     
     axios({ method: "POST", 
                     "url": this.cchsCoreConfig.APP_CONFIG.TOKEN_URI, 
                     "data": querystring.stringify({
                      grant_type: this.cchsCoreConfig.APP_CONFIG.GRANT_TYPE,
                      refresh_token: sessionStorage.getItem(this.cchsCoreConfig.APP_CONFIG.GRANT_TYPE)
                    }), 
                    "headers": { 'Accept': this.cchsCoreConfig.APP_CONFIG.ACCEPT_APP_JSON,
                                 'Content-Type': this.cchsCoreConfig.APP_CONFIG.CONTENT_TYPE_URLENCODED} 
                  }).then(result => {
                       result.data.code = this.cchsCoreConfig.APP_CONFIG.CODE;
                       result.data.state = this.cchsCoreConfig.APP_CONFIG.STATE;
                       sessionStorage.setItem(this.cchsCoreConfig.APP_CONFIG.TOKEN_RESPONSE_STRING, JSON.stringify(result.data));
                       this.cchsCoreConfig.APP_CONFIG.ACCESS_TOKEN = JSON.parse(sessionStorage.getItem(this.cchsCoreConfig.APP_CONFIG.TOKEN_RESPONSE_STRING)).access_token;
                       this.cchsCoreConfig.instance = axios.create({
                                                      baseURL: this.cchsCoreConfig.APP_CONFIG.BASE_FHIR_API_URL,
                                                      headers: {'Accept': this.cchsCoreConfig.APP_CONFIG.ACCEPT_APP_JSONFHIR, 
                                                                'Content-Type': this.cchsCoreConfig.APP_CONFIG.CONTENT_TYPE_URLENCODED, 
                                                                'Authorization': this.cchsCoreConfig.APP_CONFIG.TOKEN_TYPE +' '+ this.cchsCoreConfig.APP_CONFIG.ACCESS_TOKEN}
                                                    });
                       this.smartClient.tokenResponse = {};
                       this.smartClient.tokenResponse = result.data;
                       this.cchsCoreConfig.patient = this.patient;
                       location.reload();
                       console.log("Token Refresh Done for Axios Client and FHIR JS Client");
                    }, error => {
                        console.error(error);
                    });
    },
    dateRangeChange(range){
      console.log('THIS IS CALLED IN PARENT '+range)
      this.cchsCoreConfig.APP_CONFIG.DATE_RANGE = range;
    }
  }
}
</script>

<style>
#sidebar-wrapper {
    min-width: 250px;
    max-width: 250px;
    transition: all 0.3s;
    /* position: fixed; */
}
#sidebar-wrapper.active{
	margin-left: -250px;
}
.wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
}
#content.active{
  /* margin-left: 250px; */
}
#content{
  margin-left: 0px;
  transition: all 0.3s;
}
.btn-success{
  color: whitesmoke;
  background: darkgreen;
  border-color: darkgreen;
}
.btn-success:hover{
  color: whitesmoke;
  background: forestgreen;
  border-color: forestgreen;
}
.btn-info{
  color: #fff;
  background: #3498db;
  border-color:#3498db;
}
.btn-info:hover{
  color: whitesmoke;
  background: #068be4;
  border-color: #068be4;
}
.badge-success{
  color: whitesmoke;
  background: forestgreen;
}
.dateRangeRow{
  position: fixed;
  width: 100%;
  height: 265px;
  z-index: 10;
}
.pageContentRow{
  margin-top:320px;
}
</style>
