<template>
    <div class="card">
        <h5 class="card-header">Vitals History</h5>
        <ErrorDisplay v-if="error !== null" :errorMessage="this.error"/>
        <div v-else class="card-body">
            <HeartRateChart v-if="this.heartRateDataList !== undefined && 
                            this.heartRateDataList.length !==0" 
                            :heartRateDataList="heartRateDataList" 
                            :cchsCoreConfig="cchsCoreConfig"/>
            <BloodPressureChart v-if="this.bloodPressureDataList !== undefined && 
                            this.bloodPressureDataList.length !==0" 
                            :bloodPressureDataList="bloodPressureDataList" 
                            :cchsCoreConfig="cchsCoreConfig"/>
            <RepiratoryRateChart v-if="this.respiratoryRateDataList !== undefined && 
                            this.respiratoryRateDataList.length !==0" 
                            :respiratoryRateDataList="respiratoryRateDataList" 
                            :cchsCoreConfig="cchsCoreConfig"/>
            <TemperatureChart v-if="this.temperatureDataList !== undefined && 
                            this.temperatureDataList.length !==0" 
                            :temperatureDataList="temperatureDataList" 
                            :cchsCoreConfig="cchsCoreConfig"/>
            
        </div>
    </div>
</template>

<script>
import ErrorDisplay from "../components/ErrorDisplay";
import HeartRateChart from "../components/HeartRateChart"
import BloodPressureChart from "../components/BloodPressureChart"
import RepiratoryRateChart from "../components/RepiratoryRateChart"
import TemperatureChart from "../components/TemperatureChart"
export default {
data(){
        return{
            error: null
        }
    },
    props: ["filteredList", "cchsCoreConfig"],
    components:{ErrorDisplay, HeartRateChart, BloodPressureChart, RepiratoryRateChart, TemperatureChart},
    computed:{
        heartRateDataList: function(){
           return this.filteredList.filter(e => e.code.coding.find(c => c.code === '8867-4'))
        },
        bloodPressureDataList: function(){
           return this.filteredList.filter(e => e.code.coding.find(c => c.code === '55284-4'))
        },
        respiratoryRateDataList: function(){
           return this.filteredList.filter(e => e.code.coding.find(c => c.code === '9279-1'))
        },
        temperatureDataList: function(){
           return this.filteredList.filter(e => e.code.coding.find(c => {
               if(c.code === '8310-5' || c.code === '8331-1')
                    return c;
           }))
        }
    }
}
</script>

