<template>
<pulse-loader v-if="loading" style="margin-top: 60px;text-align: center;"></pulse-loader>
<div v-else id="vitalSigns">
    <div class="container-fluid">
        <div>
            <input class="form-control" type="text" v-model="search" placeholder="Search Vitals.."/>
        </div>
        <div class="vitalsWrapper">
             <div class="card cardWrapper" v-bind:key="getRowKey(vital)" v-for="vital in filteredList">
                <div class="card-body">
                    <a href="#"  data-toggle="modal" data-target='#exampleModel' @click="info(vital)">
                        <h5>{{vital}} <span class="badge badge-success badge-pill" style="float:right;">{{getCountofVitals(vital)}}</span></h5>
                    </a>
                </div>
            </div>
        </div>
    </div>
    
    <!-- Modal -->
    <div class="modal fade bd-example-modal-lg" id="exampleModel" tabindex="-1" role="dialog">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{infoModal.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="resetInfoModal()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <ObservationModal :infoModal="infoModal" :cchsCoreConfig="cchsCoreConfig"/>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="resetInfoModal()">Close</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
import ObservationModal from "../../components/ObservationModal";
export default {
    name: 'vitalSigns',
    components:{PulseLoader,ObservationModal},
    data(){
        return{
            loading: true,
            vitalSigns: null,
            search: null,
            vitalCodes: null,
            rowArray: [],
            infoModal: {
                title: '',
                content: '',
                isComponentTagPresent: false,
                getValuesByCodeText: []
                }
        }
    },
    props: ["patient", "smartClient","cchsCoreConfig"],
    methods:{
        getRowKey(row){
            return row+Math.random();
        },
        groupVitalSigns(){
            this.vitalCodes = Array.from(new Set(this.vitalSigns.data.entry.map(a => a.resource.code.text)));
        },
        getCountofVitals(vital){
            return this.vitalSigns.data.entry.filter(e => e.resource.code.text === vital).length
        },
        resetInfoModal() {
            this.infoModal.title = ''
            this.infoModal.getValuesByCodeText = [];
            this.infoModal.isComponentTagPresent = false;
        },
        info(vital) {
            this.infoModal.title = vital;
            this.infoModal.getValuesByCodeText = this.vitalSigns.data.entry.filter(e => e.resource.code.text === vital)
        }
    },
    async beforeMount(){
        this.vitalSigns = await this.smartClient.patient.api.search({
            type:"Observation",
            query: {patient: this.patient.id,
                    category: "vital-signs",
                    _count: 100
            }
          });
        this.groupVitalSigns();
        this.loading=false;
    },
    computed:{
        filteredList: function(){
            if(this.search){
                return this.vitalCodes.filter(e => {return e.toLowerCase().includes(this.search.toLowerCase())});
            }else{
                return this.vitalCodes;
            }
        }
    }
}
</script>

<style>
.vitalsWrapper{
  -webkit-column-count: 3; /* Chrome, Safari, Opera */
  -moz-column-count: 3; /* Firefox */
  column-count: 3;
  -webkit-column-gap: 40px; /* Chrome, Safari, Opera */
  -moz-column-gap: 40px; /* Firefox */
  column-gap: 40px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 12px;
}
.cardWrapper{
    max-width: 350px;
    width: 300px;
    height: 100px;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
}
</style>
