<template>
<div class="card">
    <h5 class="card-header">Encounters</h5>
    <div class="card-body" ref="acute">
        <EncounterTimeLine v-if="computedAcuteEncounterList.length !==0" 
                           :heading="'Acute'" 
                           :filteredEncountersList="computedAcuteEncounterList" 
                           :cchsCoreConfig="cchsCoreConfig"/>

        <EncounterTimeLine v-if="computedPrimaryCareEncountersList.length !==0" 
                           :heading="'Primary Care'" 
                           :filteredEncountersList="computedPrimaryCareEncountersList" 
                           :cchsCoreConfig="cchsCoreConfig"/>

        <EncounterTimeLine v-if="computedSpecialityEncountersList.length !==0" 
                           :heading="'Speciality'" 
                           :filteredEncountersList="computedSpecialityEncountersList" 
                           :cchsCoreConfig="cchsCoreConfig"/>
        
    </div>
</div>
</template>

<script>
import Utils from "../../cchs-utils";
import EncounterTimeLine from "./EncounterTimeLine";
import moment from 'moment'
export default {
    name:'Encounters',
    components:{EncounterTimeLine},
    props:['patient', 'cchsCoreConfig'],
    data(){
        return{
            width: null,
            offSet: 300,
            encountersList: [],
            acuteEncountersList: [],
            primaryCareEncountersList: [],
            specialityEncountersList: []
        }
    },
    methods:{
        calculateWidth() {
            this.width = this.$refs.acute.clientWidth - this.offSet;
        },
        groupEncounters(){
            let x = this.width/this.encountersList.length;
            let counter = x;
            this.acuteEncountersList = [];
            this.specialityEncountersList = [];
            this.primaryCareEncountersList = [];
            this.encountersList.forEach(e => {
                e.x = x;
                e.start = Utils.getDateInReadableMMDDYYYYFormat(e.period.start)
                if(e.location.find(c => c.location.display.indexOf('_HOSP') !== -1 || c.location.display.indexOf('Hospital') !== -1) !== undefined)
                    this.acuteEncountersList.push(e);
                else if(e.location.find(c => c.location.display.indexOf('Primary Care') !== -1) !== undefined)
                    this.primaryCareEncountersList.push(e);
                else
                    this.specialityEncountersList.push(e);
                x+=counter;
            });
        },
        resizeRectangle(e){
            //console.log('e')
            this.calculateWidth();
            this.groupEncounters();
        },
        sortEncounterListAsc(){
            this.encountersList.sort(function(a,b){
                let dateA = new Date(a.period.start);
                let dateB = new Date(b.period.start);
                if(dateA > dateB)
                    return -1;
                if(dateA < dateB)
                    return 1;
                return 0;
            });
        },
        fetchAllEncounters(){
            let params = {patient: this.patient.id}
            const fetchAllEnc = new Promise((resolve, reject) =>{
                Utils.fetchAllRecords(this.cchsCoreConfig.instance,'Encounter', params, [], resolve, reject);
            }).then(response=> {
                this.encountersList = response;
                this.sortEncounterListAsc();
                this.groupEncounters();
            });
        }
    },
    computed:{
        computedAcuteEncounterList: function(){
            if(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE !== undefined)
                return this.acuteEncountersList.filter(e=> {
                    let dateFrom = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[1]);
                    let dateTo = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[0]);
                    let check = moment(e.period.start);
                    if(check >= dateFrom && check <= dateTo)
                        return e;
                });
            else
                return acuteEncountersList;
        },
        computedSpecialityEncountersList: function(){
            if(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE !== undefined)
                return this.specialityEncountersList.filter(e=> {
                    let dateFrom = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[1]);
                    let dateTo = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[0]);
                    let check = moment(e.period.start);
                    if(check >= dateFrom && check <= dateTo)
                        return e;
                });
            else
                return specialityEncountersList;
        },
        computedPrimaryCareEncountersList: function(){
            if(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE !== undefined)
                return this.primaryCareEncountersList.filter(e=> {
                    let dateFrom = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[1]);
                    let dateTo = moment(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[0]);
                    let check = moment(e.period.start);
                    if(check >= dateFrom && check <= dateTo)
                        return e;
                });
            else
                return primaryCareEncountersList;
        }
    },
    beforeMount(){
        this.fetchAllEncounters();
    },
    mounted(){
        this.calculateWidth();
        window.addEventListener('resize', this.resizeRectangle);
    },
    beforeDestroy(){
        window.removeEventListener('resize', this.resizeRectangle);
    }
}
</script>

