<template>
    <pulse-loader v-if="loading" style="margin-top: 60px;text-align: center;"></pulse-loader>
    <div v-else class="container-fluid">
        <div class="row" :id="doc.resource.id" v-bind:key="doc.resource.id" v-for="doc in document.documentReferenceArray" style="padding-bottom: 15px;">
            <div id="doc-container" class="col-sm-12 card">
                <div class="card-body">
                    <div class="d-flex w-100 justify-content-between">
                        <h5>
                            Encounter ID: {{doc.resource.context.encounter.reference.replace("Encounter/","")}}, 
                            Type: {{doc.resource.type.text}}
                        </h5>
                        <small>{{getDateInReadableFormat(doc.resource.created)}}</small>
                    </div>
                    <div>
                        Title: {{doc.resource.description}}
                        <div class="pb-2"/>
                        Status: {{doc.resource.docStatus.text}}
                        <div class="pb-2"/>
                        <small>VerifiedBy: {{doc.resource.authenticator !== undefined ? doc.resource.authenticator.display:'NA'}}</small>
                        <div class="pb-3"/>
                    </div>
                    <a class="btn btn-success" data-toggle="collapse" :href="'#collapse_'+doc.resource.id" @click="getBinaryDocument(doc.resource.content)">Show Document</a>
                </div>

                <div class="collapse" :id="'collapse_'+doc.resource.id">
                    <pulse-loader v-if="documentCard.loadingDocument" style="padding: 15px; text-align:center;"></pulse-loader>
                    <div v-else-if="documentCard.error!==undefined && documentCard.loadingDocument===false">
                            <div class="alert alert-danger alert-dismissible fade show" role="alert">
                                <strong>{{documentCard.error}}</strong> Received From FHIR Server
                                <button type="button" class="close" data-toggle="collapse" :data-target="'#collapse_'+doc.resource.id">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                    </div>
                    <div v-else-if="documentCard.error===undefined && documentCard.loadingDocument===false">
                        <iframe v-if="is_IE" id="pdfViewer" :src="pdflocalurl" height="500" width="75%"/>                        
                        <object v-else :data="'data:application/pdf;base64,'+documentCard.base64DataResponse" type="application/pdf" height="500" width="75%"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import cchsSmartClient from '../../cchs-smart-client';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';

export default {
    components:{
        PulseLoader
    },
    props: ["cchsCoreConfig","patient"],
    data(){
        return{
            document:{
                documentReferenceArray: [],
                link: []
            },
            loading: true,
            is_IE: true,
            isActive: false,
            documentCard:{
                error: undefined,
                pdflocalurl: null,
                loadingDocument: true,
                base64DataResponse: null
            }
        }
    },
    methods:{
        findPreviousPageUrl(){
            return this.document.link.find(e=> e.relation==='previous');
        },
        fetchInitialDocuments(){
            this.cchsCoreConfig.instance.get("DocumentReference",{params:{patient: this.patient.id, _count:5}})
            .then(response=>{
                if(response.status==200){
                    this.document.documentReferenceArray = response.data.entry;
                    this.document.link = response.data.link;
                    this.loading = false;
                }
            }, error_response=>{
                console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
            });
            /* MSIE used to detect old browsers and Trident used to newer ones*/
            var ua = navigator.userAgent;
            this.is_IE = ua.indexOf("MSIE ") > -1 || ua.indexOf("Trident/") > -1;
        },
        scroll(){
            window.onscroll = () => {
                let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;
                var nextPageURI = this.document.link.find(e=> e.relation==='next')
                //console.info("nextPageURI "+ nextPageURI);
                if (bottomOfWindow && nextPageURI !== undefined) {
                    this.cchsCoreConfig.instance.get(nextPageURI.url)
                    .then(response => {
                        if(response.status==200){
                            response.data.entry.forEach(e => this.document.documentReferenceArray.push(e));
                            this.document.link = [];
                            this.document.link = response.data.link;
                            //console.info("On Scroll DocumentReference Fetch Completed");
                        }
                    }, error_response => {
                        console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
                    })
                    
                }
            };
        },
        hideDocumentCard(id){
            this.isActive = false;
        },
        getDateInReadableFormat(date){
            return new Date(date).getMonth() + 1 + '/' + new Date(date).getDate() + '/' +  new Date(date).getFullYear() +' '+ new Date(date).getHours() + ':' + new Date(date).getMinutes() +':'+ new Date(date).getSeconds() 
        },
        getBinaryDocument(content){

            this.documentCard.error = undefined;
            this.documentCard.loadingDocument = true;
            this.documentCard.pdflocalurl = undefined;
            this.documentCard.base64DataResponse = undefined;

            this.cchsCoreConfig.instance.get(content[0].attachment.url)
            .then(response=>{
                try{
                    if (response.status !== 200) {
                        this.documentCard.loadingDocument = false;
                        this.documentCard.error = response.status === undefined ? response : result.status;
                        console.error('http not 200 but : ', response.status)
                    }else {
                        console.info('connected successfully : ' + response.status)
                        this.documentCard.base64DataResponse = response.data.content;
                        if(this.is_IE){
                            let pdfData = atob(response.data.content)
                            let uint8ArrayPdf = new Uint8Array(pdfData.length)
                            for (let i = 0; i < pdfData.length; i++) {
                                uint8ArrayPdf[i] = pdfData.charCodeAt(i)
                            }
                            var blob = new Blob([uint8ArrayPdf], {type: 'application/pdf'});
                            window.navigator.msSaveOrOpenBlob(blob, 'fileName.pdf');
                            //var url = 'viewer/web/viewer.html?file=';
                            //var dataPdf  = window.URL.createObjectURL(blob);
                            //this.documentCard.pdflocalurl=url + dataPdf;
                            //this.documentCard.pdflocalurl = 'viewer/web/viewer.html';
                            //console.info("DO MAGIC "+ this.pdflocalurl);
                            //$("pdfViewer").setAttribute('src',url + encodeURIComponent(dataPdf));
                            // $("pdfViewer").contentWindow.PDFViewerApplication.open(uint8ArrayPdf);
                        }
                        this.documentCard.loadingDocument = false;
                        this.documentCard.documentLoaded = true;
                    }
                }
                catch(result){
                    this.documentCard.loadingDocument = false;
                    this.documentCard.error = result.status === undefined ? result : result.status;
                    console.error("Exception:- "+result);
                }
            },
            error_response=>{
                this.documentCard.loadingDocument = false;
                this.documentCard.error = error_response;
                console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
            });
        }
    },
    mounted(){
        this.scroll();
    },
    beforeMount(){
        this.fetchInitialDocuments();
    }
}
</script>

<style>
#doc-container h5,.card-body, small{
  color: black !important;
}
</style>
