<template>
    <div>
        <h6 class="ml-0">Blood Pressure</h6>
        <div class="chart-container">
            <canvas id="bloodPressureChart"></canvas>
        </div>
    </div>    
</template>

<script>
import ErrorDisplay from "../components/ErrorDisplay";
import Core from "../cchs_smartonfhir_core"
import Chart from 'chart.js';
export default {
    props: ["bloodPressureDataList", "cchsCoreConfig"],
    components:{ErrorDisplay, Chart},
    data(){
        return{
            error: null,
            systolicYAxis: [],
            diastolicYAxis: [],
            labelXAxis: []
        }
    },
    methods:{
        createChart(chartId, chartConfig) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
                type: chartConfig.type,
                data: chartConfig.data,
                options: chartConfig.options,
            });
        },
        createDataSet(){
            let dataSystolic = [];
            let dataDiastolic = [];
            let label = [];
            this.bloodPressureDataList.forEach(function(e) {
                dataSystolic.push(Core.getBloodPressureValue(e.component, "8480-6").value);
                dataDiastolic.push(Core.getBloodPressureValue(e.component, "8462-4").value);
                label.push(Core.getDateInReadableFormat(e.effectiveDateTime));
            });
            this.systolicYAxis = dataSystolic;
            this.diastolicYAxis = dataDiastolic;
            this.labelXAxis = label;
        }
    },
    computed:{
        lineChartConfig: function(){
            let retVal = {};
            retVal.data = {};
            retVal.options={maintainAspectRatio:false}
            retVal.options.scales={}
            retVal.type = 'line';
            retVal.data.labels =  this.labelXAxis;              
            retVal.data.datasets = [{
                data: this.systolicYAxis,
                backgroundColor:'rgba(0, 0, 255, 0.8)',
                borderColor:'rgba(0, 0, 205, 0.3)',
                label: 'Systolic',
                fill: false
            },{
                data: this.diastolicYAxis,
                backgroundColor:'rgba(139, 0, 139, 0.8)',
                borderColor:'rgba(255, 0, 255, 0.3)',
                label: 'Diastolic',
                fill: false
            }]
            retVal.options.scales.yAxes = [{gridLines:{display:true}, ticks:{maxTicksLimit: 6, callback: function(value, index, values){return value+' mmHg' ;} }}]
            retVal.options.scales.xAxes = [{gridLines:{display:false}}]
            return retVal;
        }
    },
    mounted(){
        this.createDataSet();
        this.createChart('bloodPressureChart', this.lineChartConfig);
    },
    beforeMount(){
        
    }
}
</script>

<style scoped>
.chart-container {
  position: relative;
  height: 12vw;
  width: 90%;
  padding: 10px;
  color: darkmagenta;
}
</style>
