<template>
    <div v-if="cbcPanelList !== undefined && cbcPanelList.length !== 0" class="card cardWrapper1">
        <div class="flex-container">
            <h5>CBC</h5>
        </div>
        <div class="card-body">
            <svg class="cbcPanelWrapper">
                <line x1="0" y1="0" x2="100" y2="100" class="lineStyle"/>
                <line x1="0" y1="200" x2="100" y2="100" class="lineStyle"/>
                
                <line x1="100" y1="100" x2="200" y2="100" class="lineStyle"/>
                
                <line x1="200" y1="100" x2="300" y2="0" class="lineStyle"/>
                <line x1="200" y1="100" x2="300" y2="200" class="lineStyle"/>
                
                <text x="0" y="100" class="labValue" v-bind:style="{fill: fontColor(wbcValue)}">{{wbcValue === undefined ? 'n/a' : wbcValue.valueQuantity.value}}</text>
                <text x="0" y="120" class="labUnit">{{wbcValue === undefined ? 'n/a' : wbcValue.valueQuantity.unit}}</text>
                <text x="50" y="105" class="labType">WBC</text>
                
                <text x="120" y="50" class="labValue" v-bind:style="{fill: fontColor(hgbValue)}">{{hgbValue === undefined ? 'n/a' : hgbValue.valueQuantity.value}}</text>
                <text x="120" y="70" class="labUnit">{{hgbValue === undefined ? 'n/a' : hgbValue.valueQuantity.unit}}</text>
                <text x="130" y="90" class="labType">HGB</text>
                
                
                <text x="130" y="170" class="labValue" v-bind:style="{fill: fontColor(hctValue)}">{{hctValue === undefined ? 'n/a' : hctValue.valueQuantity.value}}</text>
                <text x="140" y="140" class="labUnit">{{hctValue === undefined ? 'n/a' : hctValue.valueQuantity.unit}}</text>
                <text x="130" y="120" class="labType">HCT</text>
                
                <text x="245" y="100" class="labValue" v-bind:style="{fill: fontColor(platletesValue)}">{{platletesValue === undefined ? 'n/a' : platletesValue.valueQuantity.value}}</text>
                <text x="240" y="120" class="labUnit">{{platletesValue === undefined ? 'n/a' : platletesValue.valueQuantity.unit}}</text>
                <text x="210" y="105" class="labType">PLTS</text>
            </svg>
        </div>
    </div>
    <div  v-else-if="error !== null" class="alert alert-danger alert-dismissible fade show" role="alert">
        <strong>{{error}}</strong>
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
        </button>
    </div>
</template>

<script>
export default {
    data(){
        return{
            error: null
        }
    },
    props: ["filteredList"],
    methods:{
        getObjectByCode(value){
           return this.cbcPanelList.filter(e => e.code.coding.find(c => c.code === value)).pop();
        },
        fontColor: function(value){
            let color = "mediumblue"
                if(value!== undefined && value.hasOwnProperty('interpretation') && value.interpretation.hasOwnProperty('text'))
                    color = value.interpretation.text === 'LOW'? 'red':'mediumblue';
            return  color;
        }
    },
    computed:{
        wbcValue: function(){
            return this.getObjectByCode('6690-2') ;
        },
        hgbValue: function(){
            return this.getObjectByCode('718-7');
        },
        hctValue: function(){
            return this.getObjectByCode('4544-3');
        },
        platletesValue: function(){
            return this.getObjectByCode('777-3');
        },
        cbcPanelList: function(){
           return this.filteredList.filter(e => e.code.coding.find(c => c.code === '6690-2' || c.code === '718-7' || c.code === '4544-3' || c.code === '777-3') )
        }
    }
}
</script>

<style scoped>

.labValue{
    font-size: 28px;
}
.labType{
    font-size: 14px;
    fill: olive;
}
.labUnit{
    font-size: 12px;
    fill: olive;
}
.lineStyle{
    stroke:rgb(0,0,0);
    stroke-width:2
}
.cardWrapper1{
    width: 360px;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
}
.cbcPanelWrapper{
    height: 200px
}
.cardHeader1{
    padding-left: 140px;
    padding-top: 8px;
}
.flex-container{
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
</style>
