<template>
  <div class="card">
    <h5 class="card-header">Medications</h5>
    <div class="card-body">
         <input class="form-control" type="text" v-model="searchMeds" placeholder="Search Meds.."/>
    </div>
    <div v-bind:key="med.id" v-for="med in filteredMedications" class="card-body" ref="acute">
        <h6>{{med}}</h6>
        <MedicationTimeLine :medicationMapArr="medicationMap.get(med)" :cchsCoreConfig="cchsCoreConfig" :patient="patient"/>
    </div>
</div>
</template>

<script>
import Utils from "../../cchs-utils";
import MedicationTimeLine from "./MedicationTimeLine";
export default {
    name:'Medications',
    components:{MedicationTimeLine},
    props:['patient', 'cchsCoreConfig'],
    data(){
        return{
            medicationPanelList: [],
            medications:[],
            medicationMap: new Map(),
            searchMeds: null
        }
    },
    methods:{
        fetchAllMedicationStatement(){
            let params = {patient: this.patient.id, status:'active,completed,intended'}
            const fetchAllLabs = new Promise((resolve, reject) =>{
                Utils.fetchAllRecords(this.cchsCoreConfig.instance,'MedicationStatement', params, [], resolve, reject);
            }).then(response=> {
                this.medicationPanelList = response;
            });
        }
    },
    computed:{
        filteredmedicationPanelList(){
            if(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE !== undefined)
                return this.medicationPanelList.filter(e=> {
                    if(Utils.isWithinDateRange(this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[1],this.cchsCoreConfig.APP_CONFIG.DATE_RANGE[0], e.dateAsserted))
                        return e;
                });
            else
                return medicationPanelList;
        },
        filteredMedications(){
            this.medicationMap = new Map();
            this.medications = Array.from(new Set(this.filteredmedicationPanelList.map(element => {
                    let value;
                    let a = {};
                    if(element.hasOwnProperty('medicationReference')){
                        value = element.medicationReference.display
                    }else if(element.hasOwnProperty('medicationCodeableConcept')){
                        value = element.medicationCodeableConcept.text
                    }else{
                        value = '';
                    }
                    let label = value.startsWith('Misc Prescription') ? Utils.removeLastChar(value.replace(/Misc Prescription \(/,'')): value;
                    //a.medLabel = Utils.capitalize(label);
                    //a.medValue = value.toLowerCase();
                    label = Utils.capitalize(label.replace(/\s\((.+)\)+/,""));

                    if(this.medicationMap.get(label) !== undefined){
                        let arr = Object.values(this.medicationMap.get(label));
                        arr.push(element);
                        this.medicationMap.set(label, arr);
                    }else{
                        this.medicationMap.set(label, [element]);
                    }

                    return label;
                })));
            if(this.searchMeds)
                return this.medications.filter(e => {return e.toLowerCase().includes(this.searchMeds.toLowerCase())});
            else 
                return  this.medications;
        }
    },
    created(){
        this.fetchAllMedicationStatement();
    }
}
</script>

