<script>
import axios from "axios";

export default {
    name: 'medicalStatements',
    props: ["observation",
            "patient",
            "user",
            "tokenResponse",
            "medicationCodes",
            "medications",
            ],
    data(){
        return{
            fields: null,
            currentPage: 1,
            perPage: 5,
            totalRows: 1,
            filter: null,
            form: {
                    note: null,
                    code: null,
                    dosage: null
                  },
            formState: null,
            payloadToBePosted : null,
            isBusy: false
        }
    },
    computed:{
        getFields: function(){
            if(window.innerWidth < 568){
                return [{key: 'id', label:'Reource Id' , sortable: true},
                    {key: 'medicationCodeableConcept', label:'Medication Name' , sortable: true},
                    {key: 'actions', label: 'Actions' , sortable: true }
                   ]
            }else{
                return [{key: 'id', label:'Reource Id' , sortable: true},
                    {key: 'effectivePeriod', label:'Effective Date', sortable: true},
                    {key: 'medicationCodeableConcept', label:'Medication Name' , sortable: true},
                    {key: 'wasNotTaken', label: 'Taken' , sortable: true},
                    {key: 'status', label: 'Status' , sortable: true },
                    {key: 'actions', label: 'Actions' , sortable: true }
                   ];
            } 
        }
    },
    mounted() {
      // Set the initial number of items
     // this.totalRows = this.medications.length;
    },
    methods:{
        updateStatusForMedicationStatus(row){
            //alert('checking '+row.item.id);
            this.medications.find(e => e.id == row.item.id).status = 'Completed';
        },
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.formState = valid ? 'valid' : 'invalid'
            return valid
        },
        resetModal() {
            this.form.note = ''
            this.form.code = ''
            this.form.dosage = ''

            this.formState = null
        },
        makeMedicationStatementPayload(){
            var medCode = this.medicationCodes.find( e => e.value == this.form.code );
            var medForm = {
                resourceType: "MedicationStatement",
                patient : {reference : this.patient.resourceType + '/' + this.patient.id},
                status : "active",
                note : this.form.note,
                //informationSource: {reference: this.user.resourceType + '/' + this.user.id, display: this.user.name.text},
                medicationCodeableConcept: {coding: [{system: medCode.system, code: medCode.value, display: medCode.text, userSelected: false}], text: medCode.text},
                dosage:[{text: this.form.dosage}],
            };
            return medForm;
        },
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length
            this.currentPage = 1
        },
         handleOk(evt) {
            evt.preventDefault();
            this.isBusy = true;
            // Trigger submit handler
            this.handleSubmit()
        },
         handleSubmit() {
            // Exit when the form isn't valid
            if (!this.checkFormValidity()) {
                return
            }
            // make the payload
            this.payloadToBePosted = JSON.stringify(this.makeMedicationStatementPayload());
            //console.log("in MedicalStatement Vue:- "+ this.tokenResponse.access_token);
            //make POST request to create new medical statment
            axios({ method: "POST", 
                    "url": "https://fhir-ehr.sandboxcerner.com/dstu2/0b8a0111-e8e6-4c26-a91c-5069cbc6b1ca/MedicationStatement", 
                    "data": this.payloadToBePosted, 
                    "headers": { 'Accept': 'application/json+fhir',
                                 'Content-Type': 'application/json+fhir',
                                 'Authorization': 'Bearer'+' '+this.tokenResponse.access_token} 
                  }).then(result => {
                        if(result.status == '201'){
                            console.log("Refresh your Table:- "+result.status);
                            //location.reload();
                            this.isBusy = false;
                            //Hide the modal manually
                            this.$nextTick(() => {
                                this.$refs.modal1.hide()
                            });
                            this.$emit('refreshMedicationStatement');
                        }else{
                            console.log("Response from POST Medication:- "+result);
                            throw "Success Code not recieved";
                        }
                    }, error => {
                        console.error(error);
                    });
        }
    }
    
}
</script>

