<template>
  <div id="carouselExampleControls" class="carousel slide" data-ride="carousel">
    <div class="carousel-inner container">
      <div class="carousel-item active">
        <div class="row p-0">
          <div class="col" v-bind:key="docref.id" v-for="docref in document.documentReferenceArray">
            <div class="card">
              <div class="card-body">
                <div class="d-flex w-100 justify-content-between">
                    <h5 class="card-title">{{docref.type.text}}</h5>
                    <small>{{created_date(docref.created)}}</small>
                </div>
                <h6 class="card-subtitle mb-2 text-muted">{{docref.description}}</h6>
                <p class="card-text pb-2 pu-2">
                    By: {{docref.authenticator !== undefined ? docref.authenticator.display:'NA'}}
                    <br/>
                    <small class="text-muted">Status: {{docref.docStatus.text}}</small>
                </p>
                <a class="btn btn-info" data-toggle="collapse" href="#">Show Document</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <a class="carousel-control-prev" href="#" @click="fetchDocumentsReferences('previous')" role="button" data-slide="prev">
      <i class="fas fa-chevron-left" style="font-size:36px;color:black" aria-hidden="true"></i>
      <span class="sr-only">Previous</span>
    </a>
    <a class="carousel-control-next" href="#" @click="fetchDocumentsReferences('next')" role="button" data-slide="next">
        <i class="fas fa-chevron-right" style="font-size:36px;color:black" aria-hidden="true"></i>
        <span class="sr-only">Next</span>
    </a>
  </div>
</template>

<script>
import Utils from '../../cchs-utils'
export default {
  name: "EncounterNotes",
  props: ["selectedEncounter", "cchsCoreConfig", "document"],
  methods:{
    created_date(created){
        return Utils.getDateInReadableMMDDYYYYFormat(created);
    },
    fetchDocumentsReferences(direction){

        var pageURI = this.document.link.find(e=> e.relation===direction)

        if (pageURI !== undefined) {
            this.cchsCoreConfig.instance.get(pageURI.url)
            .then(response => {
                if(response.status==200){
                    this.document.documentReferenceArray = [];
                    this.document.documentReferenceArray = Utils.processResponseData(response.data.entry)
                    //response.data.entry.forEach(e => this.document.documentReferenceArray.push(e));
                    this.document.link = [];
                    this.document.link = response.data.link;
                }
            }, error_response => {
                console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
            })
            
        }
    }
  }
};
</script>

<style>
.carousel-control-prev{
  width: inherit;
  padding-left: 20px;
}
.carousel-control-next{
  width: inherit;
  padding-right: 20px;
}
</style>