<template>
<div v-if="chem7PanelList !== undefined && chem7PanelList.length !== 0" class="card cardWrapper2">
    <div class="flex-container">
        <h5>CHEM7</h5>
    </div>
    <div class="card-body">
        <svg class="cbcPanelWrapper2">
            <line x1="0" y1="100" x2="300" y2="100" class="lineStyle" />
            
            <line x1="100" y1="0" x2="100" y2="200" class="lineStyle" />
            <line x1="200" y1="0" x2="200" y2="200" class="lineStyle" />
            
            <line x1="300" y1="100" x2="400" y2="0" class="lineStyle" />
            <line x1="300" y1="100" x2="400" y2="200"  class="lineStyle" />

            <text x="350" y="90" class="labValue" v-bind:style="{fill: fontColor(gluValue)}">{{gluValue === undefined ? 'n/a' : gluValue.valueQuantity.value}}</text>
            <text x="350" y="120" class="labUnit">mg/dL</text>
            <text x="310" y="105" class="labType">GLU</text>

            <text x="240" y="50" class="labValue" v-bind:style="{fill: fontColor(bunValue)}">{{bunValue === undefined ? 'n/a' : bunValue.valueQuantity.value}}</text>
            <text x="240" y="70" class="labUnit">mg/dL</text>
            <text x="240" y="90" class="labType">BUN</text>


            <text x="240" y="170" class="labValue" v-bind:style="{fill: fontColor(crtValue)}">{{crtValue === undefined ? 'n/a' : crtValue.valueQuantity.value}}</text>
            <text x="240" y="140" class="labUnit">mg/dL</text>
            <text x="240" y="120" class="labType">CRT</text>


            <text x="130" y="50" class="labValue" v-bind:style="{fill: fontColor(clValue)}">{{clValue === undefined ? 'n/a' : clValue.valueQuantity.value}}</text>
            <text x="130" y="70" class="labUnit">MMOL/L</text>
            <text x="130" y="90" class="labType">CL</text>

            <text x="130" y="170" class="labValue" v-bind:style="{fill: fontColor(co2Value)}">{{co2Value === undefined ? 'n/a' : co2Value.valueQuantity.value}}</text>
            <text x="130" y="140" class="labUnit">MMOL/L</text>
            <text x="130" y="120" class="labType">CO2</text>

            <text x="30" y="50" class="labValue" v-bind:style="{fill: fontColor(naValue)}">{{naValue === undefined ? 'n/a' : naValue.valueQuantity.value}}</text>
            <text x="30" y="70" class="labUnit">MMOL/L</text>
            <text x="30" y="90" class="labType">NA</text>

            <text x="30" y="170" class="labValue" v-bind:style="{fill: fontColor(kValue)}">{{kValue === undefined ? 'n/a' : kValue.valueQuantity.value}}</text>
            <text x="30" y="140" class="labUnit">MMOL/L</text>
            <text x="30" y="120" class="labType">K</text>
            

        </svg>
    </div>
</div>

<div  v-else-if="error !== null" class="alert alert-danger alert-dismissible fade show" role="alert">
    <strong>{{error}}</strong>
    <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
    </button>
</div>
    
</template>

<script>
export default {
    data(){
        return{
            error: null
        }
    },
    props: ["filteredList"],
    methods:{
        getObjectByCode(value){
           return this.chem7PanelList.filter(e => e.code.coding.find(c => c.code === value)).pop();
        },
        fontColor: function(value){
            let color = "mediumblue"
                if(value!== undefined && value.hasOwnProperty('interpretation') && value.interpretation.hasOwnProperty('text'))
                    color = value.interpretation.text === 'LOW' ? 'red':'mediumblue';
            return  color;
        }
    },
    computed:{
        gluValue: function(){
            return this.getObjectByCode('2345-7') ;
        },
        bunValue: function(){
            return this.getObjectByCode('14937-7') || this.getObjectByCode('3094-0');
        },
        crtValue: function(){
            return this.getObjectByCode('2160-0');
        },
        clValue: function(){
            return this.getObjectByCode('2075-0');
        },
        co2Value: function(){
            return this.getObjectByCode('2028-9');
        },
        naValue: function(){
            return this.getObjectByCode('2951-2');
        },
        kValue: function(){
            return this.getObjectByCode('2823-3');
        },
        chem7PanelList: function(){
           return this.filteredList.filter(e => 
                e.code.coding.find(c => 
                c.code === '2345-7' || c.code ==='3094-0' || c.code === '14937-7' || 
                c.code === '2160-0' || c.code === '2075-0' || c.code === '2028-9' || 
                c.code === '2951-2' || c.code === '2823-3'));
        }
    }
}
</script>

<style scoped>
.labValue{
    font-size: 28px;
}
.labType{
    font-size: 14px;
    fill: olive;
}
.labUnit{
    font-size: 12px;
    fill: olive;
}
.lineStyle{
    stroke:rgb(0,0,0);
    stroke-width:2
}
.cardWrapper2{
    width: 440px;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
}
.cbcPanelWrapper2{
    height: 200px;
    width: 100%;
}
.cardHeader2{
    padding-left: 180px;
    padding-top: 8px;
}
.flex-container{
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
</style>
