<template>
    <div>
        <h6 class="ml-0">Temperature</h6>
        <div class="chart-container">
            <canvas id="temperatureChart"></canvas>
        </div>
    </div>    
</template>

<script>
import ErrorDisplay from "../components/ErrorDisplay";
import Core from "../cchs_smartonfhir_core"
import Chart from 'chart.js';
export default {
    props: ["temperatureDataList", "cchsCoreConfig"],
    components:{ErrorDisplay, Chart},
    data(){
        return{
            error: null,
            valueYAxis: [],
            labelXAxis: []
        }
    },
    methods:{
        createChart(chartId, chartConfig) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
                type: chartConfig.type,
                data: chartConfig.data,
                options: chartConfig.options,
            });
        },
        createDataSet(){
            let data = [];
            let label = [];
            this.temperatureDataList.forEach(function(e) {
                data.push(e.valueQuantity.value);
                label.push(Core.getDateInReadableFormat(e.effectiveDateTime));
            });
            this.valueYAxis = data;
            this.labelXAxis = label;
        }
    },
    computed:{
        lineChartConfig: function(){
            let retVal = {};
            retVal.data = {};
            retVal.options={maintainAspectRatio:false, legend:{display: false}}
            retVal.options.scales={}
            retVal.type = 'line';
            retVal.data.labels =  this.labelXAxis;              
            retVal.data.datasets = [{
                data: this.valueYAxis,
                backgroundColor:'rgba(0, 100, 0, 0.8)',
                borderColor:'rgba(34, 139, 34, 0.3)',
                label: 'Respiratory Rate',
                fill: false
            }]
            retVal.options.scales.yAxes = [{gridLines:{display:true}, ticks:{maxTicksLimit: 5, callback: function(value, index, values){return value+' DegC' ;} }}]
            retVal.options.scales.xAxes = [{gridLines:{display:false}}]
            return retVal;
        }
    },
    mounted(){
        this.createDataSet();
        this.createChart('temperatureChart', this.lineChartConfig);
    },
    beforeMount(){
        
    }
}
</script>

<style scoped>
.chart-container {
  position: relative;
  height: 8vw;
  width: 90%;
  padding: 10px
}
</style>
