let OUTPUT = [];
const instance = undefined;
const patient = undefined;

function getDateInReadableFormat(date){
    if(date !== undefined)
        return new Date(date).getMonth() + 1 + '/' + new Date(date).getDate() + '/' +  new Date(date).getFullYear() +' '+ new Date(date).getHours() + ':' + new Date(date).getMinutes() +':'+ new Date(date).getSeconds() 
    else
        return 'NA'
}
function getBloodPressureValue(input, code){
    let output;
        input.forEach(c =>{
            if(c.code.coding.find(p => p.code === code) !== undefined)
                output = c.valueQuantity;
        });
    return output;
}
function getRowKey(row){
    return row+Math.random();
}
function processResponseData(response){
    let processedResponse = [];
    if(response!==undefined){
        response.forEach(function(e) {
            processedResponse.push(e.resource);
          });
    }    
    return processedResponse;
}
function fetchAllData(axiosObject, link, output){
    let nextPageURI = link.find(e=> e.relation==='next')
    if(nextPageURI !== undefined){
        axiosObject.get(nextPageURI.url)
        .then(response => {
            if(response.status==200){
                return fetchAllData(axiosObject, response.data.link, output.concat(processResponseData(response.data.entry)));                
            }
        }, error_response => {
            console.error("ERROR RESPONSE FROM AXIOS INSTANCE:- "+error_response);
        })
    }else{
        return output;
    }
}
export default {
    APP_CONFIG,
    instance,
    patient,
    getDateInReadableFormat,
    getRowKey,
    processResponseData,
    getBloodPressureValue,
    fetchAllData
}